import {useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import {allPass, anyPass, filter, path, pathSatisfies, propSatisfies} from "ramda";
import {useMemo} from "react";
import * as R from "ramda";
import {useWallet} from "use-wallet";


export const BondingList: { [key: string]: string } = {
    linear: "0x5b6ac51d9b1cede0068a1b26533cace807f883ee",
    exponetial: "0x432f962d8209781da23fb37b6b59ee15de7d9841"
}

export const usePool = (
    filters: any
) => {
    const {
        request,
        data,
        status,
    } = useWait(() => HttpServer.thegraph('0xd487291e9b1a37df24df39a240c3d3bf2653f361'))
    const {account} = useWallet();

    const pools: any = useMemo(() => {
        if (!data) {
            return []
        }
        return R.compose(
            R.map((ev: any) => {
                return R.mergeDeepRight(ev, {
                    assetRecipient: account,
                    owner: {
                        id: account
                    }
                })
            }),
            R.path(['data', 'pairOwner', 'pairs'])
        )(data);
    }, [data, account])


    const productionFeeding = useMemo(() => {
        return filter(
            anyPass([
                propSatisfies(
                    (batch: string) => {
                        return path(['type', batch], filters) === true;
                    },
                    'type'
                ),
                propSatisfies(
                    (batch: string) => {
                        return path(['curve', batch], filters) === true;
                    },
                    'bondingCurve'
                ),
            ]),
            pools
        );
    }, [pools, filters])

    return {request, pools, status, productionFeeding};
}
