import styled from "styled-components";
import React from "react";
import {BalanceInput} from "./balance_input";
import {PoolType} from "../../collection/pool/nft_pool_item";
import {useUpDataPool} from "../../../provider/updata_pool_provicer";

export const TokenBalanceInput: React.FC = () => {

    const {sudo: sudoData, collections} = useUpDataPool()


    if (!collections || !sudoData) {
        return <div/>
    }

    return (
        <BalanceWidget>
            <BalanceInput
                label={'Token Balance'}
                name={'balance'}
                placeholder={'1'}
                unit={`ETH`}
                imgUrl={'eth'}
                disabledText={parseInt(sudoData.type) === PoolType.nft ? ["NFT my_pool", "has No Token Balance"] : undefined}
            />
        </BalanceWidget>
    );
}


export const BalanceWidget = styled.div`
  margin-top: 10px;

`
